.content {
  background: #f0b20a;
  padding: 20px;
}
.image img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.image button {
  width: 100px;
  height: 30px;
  border-radius: 10px;
  background: #9862bf;
  color: white;
  text-transform: uppercase;
}

.card {
  display: flex;
}

.description {
  display: flex;
  border: 2px solid #1ec9b8;
  width: 60%;
  margin: 20px 20px 40px 50px;
  justify-content: space-between;
  padding: 10px 10px 10px 10px;
  border-radius: 10px;
}

.image {
  margin: 20px;
}

